  line-height: 39px;
  width: 45px;
  height: 45px;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
  color: #36aeea;
  background-color: #fff;
  border: 4px solid #36aeea;
  border-radius: 50%;
}
.scheme__number::before {
  content: '';
  position: absolute;
  display: block;
  top: 50%;
  width: 240px;
  left: 45px;
  height: 2px;
  background-image: url("../../images/scheme/scheme__border.png");
  background-repeat: repeat-x;
  transform: rotate(180deg);
}
.scheme__number::after {
  content: '';
  position: absolute;
  display: block;
  top: 70px;
  left: -8px;
  width: 55px;
  height: 2px;
  background-image: url("../../images/scheme/scheme__border.png");
  transform: rotate(90deg);
  background-repeat: repeat-x;
}
.scheme__number--no-border::before {
  display: none;
}
.scheme__image {
  margin: 0 auto;
  margin-bottom: 25px;
  width: 180px;
  height: 180px;
  background-repeat: no-repeat;
  background-position: center;
}
.scheme__text {
  font-size: 18px;
  color: #949494;
}
.scheme__text-dark {
  color: #2c2e37;
  font-weight: 700;
}
@media (max-width: 767px) {
  .scheme__number::before {
    display: none;
  }
  .scheme__text {
    margin-bottom: 40px;
    font-size: 15px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .scheme__number::before {
    display: none;
  }
  .scheme__text {
    margin-bottom: 40px;
  }
}
.travel {
  padding-bottom: 40px;
  min-height: 1075px;
  background-color: #fff;
  background-image: url("../../images/travel/travel__bg-1.png"), url("../../images/travel/travel__bg-2.jpg");
  background-repeat: no-repeat;
  background-position: center 75px, 0 bottom;
  background-size: 100%;
}
.travel__title {
  margin-top: 55px;
